import React from 'react';
import '../scss/Person.scss';
import person from '../img/person.png'; 

function Person() {
    return (
        <section className='person'>
            <div className='inner'>
                <div className='content'>
                    <h1>호평왕조 제 1대 황제</h1>
                    <p>부업 치과의사</p>
                </div>
                {
                    iter().map((val,idx) => {
                        return (
                            <img src={person} style={{"--i":idx+1, "--x":Math.cos(val), "--y":Math.sin(val)}} />
                        )
                    })
                }
                
            </div>
        </section>
    )

    function iter() {
        let result = []
        for (let i=1; i<16; i++) {
            result.push(360/15 * i *Math.PI/180)
        }
        return result
    }
}

export default Person;